import React, { useState, useMemo, useEffect } from 'react';
import { Search, Download, Upload, AlertCircle, CheckCircle, Pill, Activity, Clock } from 'lucide-react';
import { buildDatabase, databaseSize } from '../data/ndcDatabase';

const RXToICD10Generator = () => {
  const [inputCodes, setInputCodes] = useState('');
  const [results, setResults] = useState([]);
  const [searchTerm, setSearchTerm] = useState('');
  const [ndcDatabase, setNdcDatabase] = useState(new Map());
  const [databaseLoading, setDatabaseLoading] = useState(true);
  const [databaseError, setDatabaseError] = useState(null);

  // Load NDC database with progressive loading
  useEffect(() => {
    const loadNdcDatabase = async () => {
      try {
        setDatabaseLoading(true);
        setDatabaseError(null);
        
        const database = await buildDatabase();
        setNdcDatabase(database);
      } catch (error) {
        console.error('Error loading NDC database:', error);
        setDatabaseError(error.message);
      } finally {
        setDatabaseLoading(false);
      }
    };

    loadNdcDatabase();
  }, []);



  // Legacy RX code to ICD-10 mappings for backwards compatibility
  const rxToIcd10Mappings = {
    // DIABETES MEDICATIONS (68600-68699)
    '68645': ['E11.9', 'E11.65', 'E11.00'], // Metformin
    '68646': ['E10.9', 'E11.9', 'E11.65'], // Insulin (various)
    '68647': ['E11.9', 'E11.65'], // Glipizide
    '68720': ['I10', 'I11.9', 'I12.9', 'I25.10'], // Lisinopril
    '69100': ['F32.9', 'F33.9', 'F41.1'], // Sertraline
    '69200': ['J45.9', 'J44.1', 'J44.0'], // Albuterol
    '69400': ['K21.9', 'K25.9', 'K27.9'], // Omeprazole
    '69500': ['E03.9', 'E89.0'], // Levothyroxine
  };

  // Comprehensive medication database with detailed descriptions
  const medicationDatabase = {
    '68645': { name: 'Metformin', strength: '500mg', class: 'Biguanide', indication: 'Type 2 Diabetes' },
    '68646': { name: 'Insulin', strength: 'Various', class: 'Hormone', indication: 'Diabetes Mellitus' },
    '68647': { name: 'Glipizide', strength: '5mg', class: 'Sulfonylurea', indication: 'Type 2 Diabetes' },
    '68720': { name: 'Lisinopril', strength: '10mg', class: 'ACE Inhibitor', indication: 'Hypertension, Heart Failure' },
    '69100': { name: 'Sertraline', strength: '50mg', class: 'SSRI', indication: 'Depression, Anxiety' },
    '69200': { name: 'Albuterol', strength: 'Inhaler', class: 'Beta2 Agonist', indication: 'Asthma, COPD' },
    '69400': { name: 'Omeprazole', strength: '20mg', class: 'PPI', indication: 'GERD, Ulcers' },
    '69500': { name: 'Levothyroxine', strength: '50mcg', class: 'Thyroid Hormone', indication: 'Hypothyroidism' },
  };

  // ICD-10 code descriptions
  const icd10Descriptions = {
    'E11.9': 'Type 2 diabetes mellitus without complications',
    'E11.65': 'Type 2 diabetes mellitus with hyperglycemia',
    'E11.00': 'Type 2 diabetes mellitus with hyperosmolarity without coma',
    'E10.9': 'Type 1 diabetes mellitus without complications',
    'I10': 'Essential (primary) hypertension',
    'I11.9': 'Hypertensive heart disease without heart failure',
    'I12.9': 'Hypertensive chronic kidney disease with stage 1-4 or unspecified chronic kidney disease',
    'I25.10': 'Atherosclerotic heart disease of native coronary artery without angina pectoris',
    'F32.9': 'Major depressive disorder, single episode, unspecified',
    'F33.9': 'Major depressive disorder, recurrent, unspecified',
    'F41.1': 'Generalized anxiety disorder',
    'J45.9': 'Asthma, unspecified',
    'J44.1': 'Chronic obstructive pulmonary disease with acute exacerbation',
    'J44.0': 'Chronic obstructive pulmonary disease with acute lower respiratory infection',
    'K21.9': 'Gastro-esophageal reflux disease without esophagitis',
    'K25.9': 'Gastric ulcer, unspecified as acute or chronic, without hemorrhage or perforation',
    'K27.9': 'Peptic ulcer, site unspecified',
    'E03.9': 'Hypothyroidism, unspecified',
    'E89.0': 'Postprocedural hypothyroidism'
  };

  const processInputCodes = () => {
    if (databaseLoading) return;
    if (!inputCodes || typeof inputCodes !== 'string') return;
    
    const codes = inputCodes.split(/[\n,;]/).map(code => code.trim()).filter(code => code);
    const processedResults = [];

    codes.forEach(inputCode => {
      // First try to find in NDC database
      const cleanCode = inputCode.replace(/[-\s]/g, '');
      let medicationData = ndcDatabase.get(inputCode) || ndcDatabase.get(cleanCode);
      
      // Try partial matching for different NDC formats
      if (!medicationData) {
        for (const [key, value] of ndcDatabase.entries()) {
          if (key.replace(/[-\s]/g, '') === cleanCode || 
              cleanCode.includes(key.replace(/[-\s]/g, '')) ||
              key.replace(/[-\s]/g, '').includes(cleanCode)) {
            medicationData = value;
            break;
          }
        }
      }

      if (medicationData) {
        // Found in NDC database
        const icdMappings = medicationData.icdMappings || [];
        const icdDetails = icdMappings.map(mapping => ({
          code: mapping.icd10_code || '',
          description: mapping.description || '',
          confidence: mapping.confidence || 0,
          context: mapping.context || ''
        }));
        
        processedResults.push({
          rxCode: inputCode,
          medication: {
            name: medicationData.proprietaryName || medicationData.nonproprietaryName,
            strength: 'Not available',
            class: 'Unknown',
            indication: 'Prescription medication'
          },
          status: 'found',
          icdCodes: icdDetails,
          confidence: medicationData.mappingConfidence
        });
      } else {
        // Fallback to legacy mapping system
        const legacyMedication = medicationDatabase[inputCode];
        const matchingIcdCodes = rxToIcd10Mappings[inputCode] || [];
        
        if (matchingIcdCodes.length > 0 && legacyMedication) {
          const icdDetails = matchingIcdCodes.map(icdCode => ({
            code: icdCode,
            description: icd10Descriptions[icdCode] || 'Description not available'
          }));
          
          processedResults.push({
            rxCode: inputCode,
            medication: legacyMedication,
            status: 'found',
            icdCodes: icdDetails
          });
        } else if (legacyMedication) {
          const suggestedIcd = generateSuggestedIcdCodes(legacyMedication);
          processedResults.push({
            rxCode: inputCode,
            medication: legacyMedication,
            status: 'suggested',
            icdCodes: suggestedIcd
          });
        } else {
          processedResults.push({
            rxCode: inputCode,
            medication: { name: 'Unknown medication', strength: 'N/A', class: 'Unknown', indication: 'Manual review required' },
            status: 'unknown',
            icdCodes: [{ code: 'Review required', description: 'Code not found in database' }]
          });
        }
      }
    });

    setResults(processedResults);
  };

  const generateSuggestedIcdCodes = (medication) => {
    const suggestions = [];
    const medClass = medication.class.toLowerCase();
    const indication = medication.indication.toLowerCase();

    // Generate suggestions based on medication class and indication
    if (medClass.includes('statin') || indication.includes('cholesterol')) {
      suggestions.push({ code: 'E78.5', description: 'Hyperlipidemia, unspecified (suggested)' });
    }
    if (medClass.includes('ace inhibitor') || medClass.includes('arb') || indication.includes('hypertension')) {
      suggestions.push({ code: 'I10', description: 'Essential hypertension (suggested)' });
    }
    if (medClass.includes('ssri') || indication.includes('depression')) {
      suggestions.push({ code: 'F32.9', description: 'Major depressive disorder, unspecified (suggested)' });
    }
    if (indication.includes('anxiety')) {
      suggestions.push({ code: 'F41.1', description: 'Generalized anxiety disorder (suggested)' });
    }
    if (indication.includes('diabetes') || medClass.includes('biguanide')) {
      suggestions.push({ code: 'E11.9', description: 'Type 2 diabetes mellitus (suggested)' });
    }
    if (indication.includes('asthma') || medClass.includes('beta2')) {
      suggestions.push({ code: 'J45.9', description: 'Asthma, unspecified (suggested)' });
    }
    if (indication.includes('pain') || medClass.includes('nsaid')) {
      suggestions.push({ code: 'M25.50', description: 'Pain in unspecified joint (suggested)' });
    }

    return suggestions.length > 0 ? suggestions : [{ code: 'Manual review', description: 'Unable to suggest appropriate ICD-10 code' }];
  };

  const filteredResults = useMemo(() => {
    if (!searchTerm) return results;
    return results.filter(result => 
      result.rxCode.includes(searchTerm) ||
      result.medication.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
      result.medication.class.toLowerCase().includes(searchTerm.toLowerCase()) ||
      result.icdCodes.some(icd => 
        icd.code.includes(searchTerm.toUpperCase()) || 
        icd.description.toLowerCase().includes(searchTerm.toLowerCase()) ||
        (icd.context && icd.context.toLowerCase().includes(searchTerm.toLowerCase()))
      )
    );
  }, [results, searchTerm]);

  const exportResults = () => {
    const csvContent = [
      ['Code', 'Medication Name', 'Strength', 'Class', 'Indication', 'ICD-10 Code', 'ICD-10 Description', 'Confidence', 'Context', 'Status'],
      ...results.flatMap(result => 
        result.icdCodes.map(icd => [
          result.rxCode,
          result.medication.name,
          result.medication.strength,
          result.medication.class,
          result.medication.indication,
          icd.code,
          icd.description.replace(' (suggested)', ''),
          icd.confidence ? (icd.confidence * 100).toFixed(0) + '%' : 'N/A',
          icd.context || 'N/A',
          result.status
        ])
      )
    ].map(row => row.join(',')).join('\n');

    const blob = new Blob([csvContent], { type: 'text/csv' });
    const url = URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = `medication_icd10_mappings_${new Date().toISOString().split('T')[0]}.csv`;
    a.click();
    URL.revokeObjectURL(url);
  };

  if (databaseLoading) {
    return (
      <div className="max-w-6xl mx-auto p-6 bg-white">
        <div className="flex items-center justify-center py-12">
          <Clock className="animate-spin w-8 h-8 text-blue-500 mr-3" />
          <div>
            <p className="text-lg font-medium">Loading NDC Database...</p>
            <p className="text-sm text-gray-600">Please wait while we load the medication data</p>
          </div>
        </div>
      </div>
    );
  }

  if (databaseError) {
    return (
      <div className="max-w-6xl mx-auto p-6 bg-white">
        <div className="bg-red-50 border border-red-200 rounded-lg p-4">
          <div className="flex items-center">
            <AlertCircle className="w-5 h-5 text-red-500 mr-2" />
            <h3 className="font-semibold text-red-900">Database Loading Error</h3>
          </div>
          <p className="text-red-800 mt-2">{databaseError}</p>
          <p className="text-red-700 text-sm mt-1">Please check that the NDC database file is available.</p>
        </div>
      </div>
    );
  }

  return (
    <div className="max-w-6xl mx-auto p-6 bg-white">
      <div className="mb-8">
        <h1 className="text-3xl font-bold text-gray-900 mb-2">Medication Code to ICD-10 Generator</h1>
        <p className="text-gray-600">Generate corresponding ICD-10 diagnosis codes for prescription medications using NDC codes</p>
        <div className="mt-2 text-sm text-blue-600">
          Database loaded: {databaseLoading ? 'Loading...' : ndcDatabase.size.toLocaleString()} of {databaseSize.toLocaleString()} FDA-approved medications available
        </div>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6 mb-6">
        <div className="space-y-4">
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-2">
              Enter NDC or Medication Codes (one per line or comma-separated)
            </label>
            <textarea
              value={inputCodes}
              onChange={(e) => setInputCodes(e.target.value)}
              placeholder="0002-0152&#10;0002-1152&#10;68645&#10;68720"
              className="w-full h-32 p-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
              rows={6}
            />
          </div>
          
          <button
            onClick={processInputCodes}
            disabled={databaseLoading}
            className="w-full bg-blue-600 text-white px-6 py-3 rounded-lg hover:bg-blue-700 disabled:opacity-50 disabled:cursor-not-allowed transition-colors font-medium"
          >
            {databaseLoading ? 'Loading...' : 'Generate ICD-10 Codes'}
          </button>
        </div>

        <div className="bg-gray-50 p-4 rounded-lg">
          <h3 className="font-semibold text-gray-900 mb-3">Instructions</h3>
          <ul className="space-y-2 text-sm text-gray-600">
            <li>• Enter NDC codes or medication codes one per line or separated by commas</li>
            <li>• Database includes {ndcDatabase.size.toLocaleString()} real FDA-approved medications</li>
            <li>• Each medication includes multiple ICD-10 mappings with confidence scores</li>
            <li>• Confidence scores indicate mapping reliability (High: &ge;70%, Medium: 40-69%, Low: &lt;40%)</li>
            <li>• Review all mappings with healthcare providers</li>
            <li>• Export results to CSV for documentation</li>
          </ul>
          
          <div className="mt-4 p-3 bg-blue-50 rounded border border-blue-200">
            <p className="text-sm text-blue-800">
              <strong>Sample Codes:</strong><br/>
              0002-0152 (Zepbound), 0002-1152 (Mounjaro)<br/>
              68645 (Legacy: Metformin), 68720 (Legacy: Lisinopril)<br/>
              Try both NDC format and legacy codes
            </p>
          </div>
        </div>
      </div>

      {results.length > 0 && (
        <div className="space-y-4">
          <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
            <h2 className="text-xl font-semibold text-gray-900">
              Generated Results ({results.length} codes processed)
            </h2>
            <div className="flex gap-3">
              <div className="relative">
                <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400 w-4 h-4" />
                <input
                  type="text"
                  placeholder="Search results..."
                  value={searchTerm}
                  onChange={(e) => setSearchTerm(e.target.value)}
                  className="pl-10 pr-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                />
              </div>
              <button
                onClick={exportResults}
                className="flex items-center gap-2 bg-green-600 text-white px-4 py-2 rounded-lg hover:bg-green-700 transition-colors"
              >
                <Download className="w-4 h-4" />
                Export CSV
              </button>
            </div>
          </div>

          <div className="grid gap-4">
            {filteredResults.map((result, index) => (
              <div key={index} className="border border-gray-200 rounded-lg p-4 bg-white shadow-sm">
                <div className="flex items-start justify-between mb-4">
                  <div className="flex items-start gap-3">
                    <div className="flex items-center justify-center w-10 h-10 bg-purple-100 rounded-lg">
                      <Pill className="w-5 h-5 text-purple-600" />
                    </div>
                    <div>
                      <div className="flex items-center gap-2 mb-1">
                        <span className="font-mono text-lg font-semibold text-purple-600">
                          {result.rxCode}
                        </span>
                        {result.status === 'found' ? (
                          <CheckCircle className="w-5 h-5 text-green-500" />
                        ) : result.status === 'suggested' ? (
                          <AlertCircle className="w-5 h-5 text-orange-500" />
                        ) : (
                          <AlertCircle className="w-5 h-5 text-red-500" />
                        )}
                      </div>
                      <h3 className="font-semibold text-gray-900 text-lg">{result.medication.name}</h3>
                      <div className="flex flex-wrap gap-2 mt-1">
                        <span className="px-2 py-1 bg-gray-100 text-gray-700 text-xs rounded">
                          {result.medication.strength}
                        </span>
                        <span className="px-2 py-1 bg-blue-100 text-blue-700 text-xs rounded">
                          {result.medication.class}
                        </span>
                      </div>
                      <p className="text-sm text-gray-600 mt-1">{result.medication.indication}</p>
                    </div>
                  </div>
                  <span className={`px-2 py-1 rounded text-xs font-medium ${
                    result.status === 'found' 
                      ? 'bg-green-100 text-green-800' 
                      : result.status === 'suggested'
                      ? 'bg-orange-100 text-orange-800'
                      : 'bg-red-100 text-red-800'
                  }`}>
                    {result.status === 'found' ? 'Mapped' : result.status === 'suggested' ? 'Suggested' : 'Unknown'}
                  </span>
                </div>

                <div className="space-y-2">
                  <div className="flex items-center gap-2">
                    <Activity className="w-4 h-4 text-gray-500" />
                    <h4 className="font-medium text-gray-900">Associated ICD-10 Codes:</h4>
                  </div>
                  <div className="grid gap-2">
                    {result.icdCodes.map((icd, icdIndex) => (
                      <div key={icdIndex} className="flex items-start justify-between p-3 bg-gray-50 rounded border-l-4 border-blue-400">
                        <div className="flex-1">
                          <div className="flex items-center gap-2 mb-1">
                            <span className="font-mono font-semibold text-blue-600">
                              {icd.code}
                            </span>
                            {icd.confidence && (
                              <span className={`px-1 py-0.5 text-xs rounded ${
                                icd.confidence >= 0.7 ? 'bg-green-100 text-green-600' :
                                icd.confidence >= 0.4 ? 'bg-yellow-100 text-yellow-600' :
                                'bg-red-100 text-red-600'
                              }`}>
                                {(icd.confidence * 100).toFixed(0)}%
                              </span>
                            )}
                            {icd.description.includes('(suggested)') && (
                              <span className="px-1 py-0.5 bg-orange-100 text-orange-600 text-xs rounded">
                                SUGGESTED
                              </span>
                            )}
                          </div>
                          <p className="text-gray-700 text-sm">{icd.description}</p>
                          {icd.context && (
                            <p className="text-gray-500 text-xs italic">Context: {icd.context}</p>
                          )}
                        </div>
                      </div>
                    ))}
                  </div>
                </div>
              </div>
            ))}
          </div>
        </div>
      )}

      {results.length === 0 && (
        <div className="text-center py-12 text-gray-500">
          <Upload className="w-12 h-12 mx-auto mb-4 text-gray-300" />
          <p>Enter medication codes above to generate corresponding ICD-10 codes</p>
          <p className="text-sm mt-2">Database includes {ndcDatabase.size.toLocaleString()} medications across all therapeutic areas</p>
        </div>
      )}

      <div className="mt-8 bg-blue-50 border border-blue-200 rounded-lg p-4">
        <h3 className="font-semibold text-blue-900 mb-2">NDC Database Coverage</h3>
        <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-2 text-sm text-blue-800">
          <div>• All FDA-approved drugs</div>
          <div>• Brand and generic names</div>
          <div>• Multiple strengths/formulations</div>
          <div>• Pharmaceutical classes</div>
          <div>• Route of administration</div>
          <div>• Dosage forms</div>
          <div>• Confidence-scored mappings</div>
          <div>• Multiple ICD-10 codes per drug</div>
        </div>
        <p className="text-sm text-blue-800 mt-3">
          <strong>Total:</strong> {ndcDatabase.size.toLocaleString()} medications with real NDC codes and evidence-based ICD-10 mappings (from {databaseSize.toLocaleString()} FDA records).
        </p>
      </div>

      <div className="mt-6 bg-amber-50 border border-amber-200 rounded-lg p-4">
        <h3 className="font-semibold text-amber-900 mb-2">Clinical Documentation Notes</h3>
        <ul className="text-sm text-amber-800 space-y-1">
          <li>• One medication may be prescribed for multiple conditions (multiple ICD-10 codes with confidence scores)</li>
          <li>• Review patient-specific conditions before finalizing diagnosis codes</li>
          <li>• High confidence mappings (&ge;70%) are evidence-based, medium (40-69%) and low (&lt;40%) require validation</li>
          <li>• Consider comorbidities and contraindications when prescribing</li>
          <li>• Database includes real FDA NDC codes with pharmaceutical class information</li>
        </ul>
      </div>
    </div>
  );
};

export default RXToICD10Generator;