import React, { useState, useMemo, useEffect } from 'react';
import { Search, Download, Upload, AlertCircle, CheckCircle, Clock } from 'lucide-react';
import { buildDatabaseWithIndex, databaseSize } from '../data/ndcDatabase';

const ICD10ToRXGenerator = () => {
  const [inputCodes, setInputCodes] = useState('');
  const [results, setResults] = useState([]);
  const [searchTerm, setSearchTerm] = useState('');
  const [ndcDatabase, setNdcDatabase] = useState(new Map());
  const [icd10ToNdcIndex, setIcd10ToNdcIndex] = useState(new Map());
  const [databaseLoading, setDatabaseLoading] = useState(true);
  const [databaseError, setDatabaseError] = useState(null);

  // Load NDC database and build reverse index with progressive loading
  useEffect(() => {
    const loadNdcDatabase = async () => {
      try {
        setDatabaseLoading(true);
        setDatabaseError(null);
        
        const { database, icd10Index } = await buildDatabaseWithIndex();
        setNdcDatabase(database);
        setIcd10ToNdcIndex(icd10Index);
      } catch (error) {
        console.error('Error loading NDC database:', error);
        setDatabaseError(error.message);
      } finally {
        setDatabaseLoading(false);
      }
    };

    loadNdcDatabase();
  }, []);

  const parseNdcJsonWithIndex_DEPRECATED = (jsonData) => {
    const database = new Map();
    const icd10Index = new Map();

    jsonData.forEach(row => {
      const ndc = row.NDC;
      if (!ndc) return;

      const proprietaryName = row.PROPRIETARYNAME || '';
      const nonproprietaryName = row.NONPROPRIETARYNAME || '';
      const primaryIcd10 = row.PRIMARY_ICD10 || '';
      const allIcd10Mappings = row.ALL_ICD10_MAPPINGS || '';
      const mappingConfidence = parseFloat(row.MAPPING_CONFIDENCE) || 0;

      // Parse the JSON ICD-10 mappings
      let icdMappings = [];
      if (allIcd10Mappings && allIcd10Mappings.includes('[')) {
        try {
          // Remove surrounding quotes and unescape inner quotes
          let jsonString = allIcd10Mappings;
          if (jsonString.startsWith('"') && jsonString.endsWith('"')) {
            jsonString = jsonString.slice(1, -1);
          }
          jsonString = jsonString.replace(/""/g, '"');
          icdMappings = JSON.parse(jsonString);
        } catch (e) {
          if (primaryIcd10) {
            icdMappings = [{
              icd10_code: primaryIcd10,
              description: 'Primary diagnosis code',
              confidence: mappingConfidence,
              context: 'primary indication'
            }];
          }
        }
      } else if (primaryIcd10) {
        icdMappings = [{
          icd10_code: primaryIcd10,
          description: 'Primary diagnosis code',
          confidence: mappingConfidence,
          context: 'primary indication'
        }];
      }

      const medicationData = {
        ndc,
        proprietaryName,
        nonproprietaryName,
        primaryIcd10,
        icdMappings,
        mappingConfidence
      };

      database.set(ndc, medicationData);

      // Build reverse index: ICD-10 codes to NDC codes
      icdMappings.forEach(mapping => {
        const icd10Code = mapping.icd10_code;
        if (!icd10Index.has(icd10Code)) {
          icd10Index.set(icd10Code, []);
        }
        icd10Index.get(icd10Code).push({
          ndc,
          medication: medicationData,
          confidence: mapping.confidence,
          context: mapping.context
        });
      });
    });

    // Sort medications by confidence for each ICD-10 code
    for (const [icd10, medications] of icd10Index.entries()) {
      medications.sort((a, b) => (b.confidence || 0) - (a.confidence || 0));
    }

    return { database, icd10Index };
  };

  const parseNdcTsvWithIndex = (tsvText) => {
    const lines = tsvText.split('\n');
    const database = new Map();
    const icd10Index = new Map();

    for (let i = 1; i < lines.length; i++) {
      const line = lines[i].trim();
      if (!line) continue;

      const columns = line.split('\t');
      if (columns.length < 20) continue;

      const ndc = columns[0];
      const proprietaryName = columns[2] || '';
      const nonproprietaryName = columns[4] || '';
      const dosageForm = columns[5] || '';
      const route = columns[6] || '';
      const strength = columns[13] || '';
      const unit = columns[14] || '';
      const pharmClasses = columns[15] || '';
      const primaryIcd10 = columns[19] || '';
      const allIcd10Mappings = columns[20] || '';
      const mappingConfidence = parseFloat(columns[21]) || 0;

      // Parse the JSON ICD-10 mappings
      let icdMappings = [];
      if (allIcd10Mappings && allIcd10Mappings.startsWith('[')) {
        try {
          icdMappings = JSON.parse(allIcd10Mappings);
        } catch (e) {
          if (primaryIcd10) {
            icdMappings = [{
              icd10_code: primaryIcd10,
              description: 'Primary diagnosis code',
              confidence: mappingConfidence,
              context: 'primary indication'
            }];
          }
        }
      } else if (primaryIcd10) {
        icdMappings = [{
          icd10_code: primaryIcd10,
          description: 'Primary diagnosis code',
          confidence: mappingConfidence,
          context: 'primary indication'
        }];
      }

      const medicationData = {
        ndc,
        proprietaryName,
        nonproprietaryName,
        primaryIcd10,
        icdMappings,
        mappingConfidence
      };

      database.set(ndc, medicationData);

      // Build reverse index: ICD-10 codes to NDC codes
      icdMappings.forEach(mapping => {
        const icd10Code = mapping.icd10_code;
        if (!icd10Index.has(icd10Code)) {
          icd10Index.set(icd10Code, []);
        }
        icd10Index.get(icd10Code).push({
          ndc,
          medication: medicationData,
          confidence: mapping.confidence,
          context: mapping.context
        });
      });
    }

    // Sort medications by confidence for each ICD-10 code
    for (const [icd10, medications] of icd10Index.entries()) {
      medications.sort((a, b) => (b.confidence || 0) - (a.confidence || 0));
    }

    return { database, icd10Index };
  };

  // Legacy ICD-10 to RX code mappings for backwards compatibility
  const icd10ToRxMappings = {
    'E11.9': ['68645', '68646', '68647'], // Type 2 diabetes
    'E10.9': ['68646', '68648', '68649'], // Type 1 diabetes
    'I10': ['68720', '68721', '68722'], // Hypertension
    'F32.9': ['69100', '69101', '69102'], // Depression
    'J45.9': ['69200', '69201', '69202'], // Asthma
    'K21.9': ['69400', '69401'], // GERD
    'E03.9': ['69500', '69501'], // Hypothyroidism
  };

  // Legacy medication database
  const medicationDatabase = {
    '68645': { name: 'Metformin', strength: '500mg', class: 'Biguanide', indication: 'Type 2 Diabetes' },
    '68646': { name: 'Insulin', strength: 'Various', class: 'Hormone', indication: 'Diabetes Mellitus' },
    '68647': { name: 'Glipizide', strength: '5mg', class: 'Sulfonylurea', indication: 'Type 2 Diabetes' },
    '68720': { name: 'Lisinopril', strength: '10mg', class: 'ACE Inhibitor', indication: 'Hypertension' },
    '69100': { name: 'Sertraline', strength: '50mg', class: 'SSRI', indication: 'Depression' },
    '69200': { name: 'Albuterol', strength: 'Inhaler', class: 'Beta2 Agonist', indication: 'Asthma' },
    '69400': { name: 'Omeprazole', strength: '20mg', class: 'PPI', indication: 'GERD' },
    '69500': { name: 'Levothyroxine', strength: '50mcg', class: 'Thyroid Hormone', indication: 'Hypothyroidism' },
  };

  // ICD-10 descriptions
  const icd10Descriptions = {
    'E11.9': 'Type 2 diabetes mellitus without complications',
    'E10.9': 'Type 1 diabetes mellitus without complications',
    'I10': 'Essential (primary) hypertension',
    'F32.9': 'Major depressive disorder, single episode, unspecified',
    'J45.9': 'Asthma, unspecified',
    'K21.9': 'Gastro-esophageal reflux disease without esophagitis',
    'E03.9': 'Hypothyroidism, unspecified'
  };

  const processInputCodes = () => {
    if (databaseLoading) return;
    if (!inputCodes || typeof inputCodes !== 'string') return;
    
    const codes = inputCodes.split(/[\n,;]/).map(code => code.trim().toUpperCase()).filter(code => code);
    const processedResults = [];

    codes.forEach(icd10Code => {
      // First try to find in NDC database
      const ndcMedications = icd10ToNdcIndex.get(icd10Code) || [];
      const icd10Description = icd10Descriptions[icd10Code] || 'Description not available';
      
      if (ndcMedications.length > 0) {
        // Found in NDC database - use top medications sorted by confidence
        const medicationDetails = ndcMedications.slice(0, 10).map(item => ({
          rxCode: item.ndc,
          name: item.proprietaryName || item.nonproprietaryName,
          strength: 'Not available',
          class: 'Unknown',
          indication: 'Prescription medication',
          confidence: item.mappingConfidence || 0,
          context: 'FDA database'
        }));
        
        processedResults.push({
          icd10Code,
          description: icd10Description,
          status: 'found',
          medications: medicationDetails,
          totalFound: ndcMedications.length
        });
      } else {
        // Fallback to legacy mapping system
        const matchingRxCodes = icd10ToRxMappings[icd10Code] || [];
        
        if (matchingRxCodes.length > 0) {
          const medicationDetails = matchingRxCodes.map(rxCode => {
            const medication = medicationDatabase[rxCode];
            return {
              rxCode,
              name: medication ? medication.name : 'Unknown medication',
              strength: medication ? medication.strength : 'N/A',
              class: medication ? medication.class : 'Unknown',
              indication: medication ? medication.indication : 'N/A'
            };
          });
          
          processedResults.push({
            icd10Code,
            description: icd10Description,
            status: 'found',
            medications: medicationDetails
          });
        } else {
          // Suggest medications based on ICD-10 code patterns
          const suggestedMedications = generateSuggestedMedications(icd10Code);
          processedResults.push({
            icd10Code,
            description: icd10Description,
            status: suggestedMedications.length > 0 ? 'suggested' : 'unknown',
            medications: suggestedMedications
          });
        }
      }
    });

    setResults(processedResults);
  };

  const generateSuggestedMedications = (icd10Code) => {
    const suggestions = [];
    
    // Basic pattern matching for common ICD-10 prefixes
    if (icd10Code.startsWith('E1')) {
      suggestions.push({ rxCode: '68645', name: 'Metformin (suggested)', strength: '500mg', class: 'Biguanide', indication: 'Diabetes' });
    }
    if (icd10Code.startsWith('I')) {
      suggestions.push({ rxCode: '68720', name: 'Lisinopril (suggested)', strength: '10mg', class: 'ACE Inhibitor', indication: 'Cardiovascular' });
    }
    if (icd10Code.startsWith('F3') || icd10Code.startsWith('F4')) {
      suggestions.push({ rxCode: '69100', name: 'Sertraline (suggested)', strength: '50mg', class: 'SSRI', indication: 'Mental Health' });
    }
    if (icd10Code.startsWith('J4')) {
      suggestions.push({ rxCode: '69200', name: 'Albuterol (suggested)', strength: 'Inhaler', class: 'Beta2 Agonist', indication: 'Respiratory' });
    }
    if (icd10Code.startsWith('K')) {
      suggestions.push({ rxCode: '69400', name: 'Omeprazole (suggested)', strength: '20mg', class: 'PPI', indication: 'Gastrointestinal' });
    }

    return suggestions;
  };

  const filteredResults = useMemo(() => {
    if (!searchTerm) return results;
    return results.filter(result => 
      result.icd10Code.includes(searchTerm.toUpperCase()) ||
      result.description.toLowerCase().includes(searchTerm.toLowerCase()) ||
      result.medications.some(med => 
        med.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
        med.class.toLowerCase().includes(searchTerm.toLowerCase()) ||
        med.rxCode.includes(searchTerm)
      )
    );
  }, [results, searchTerm]);

  const exportResults = () => {
    const csvContent = [
      ['ICD-10 Code', 'ICD-10 Description', 'NDC/RX Code', 'Medication Name', 'Strength', 'Class', 'Indication', 'Confidence', 'Context', 'Status'],
      ...results.flatMap(result => 
        result.medications.map(med => [
          result.icd10Code,
          result.description,
          med.rxCode,
          med.name,
          med.strength,
          med.class,
          med.indication,
          med.confidence ? (med.confidence * 100).toFixed(0) + '%' : 'N/A',
          med.context || 'N/A',
          result.status
        ])
      )
    ].map(row => row.join(',')).join('\n');

    const blob = new Blob([csvContent], { type: 'text/csv' });
    const url = URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = `icd10_to_medications_${new Date().toISOString().split('T')[0]}.csv`;
    a.click();
    URL.revokeObjectURL(url);
  };

  if (databaseLoading) {
    return (
      <div className="max-w-6xl mx-auto p-6 bg-white">
        <div className="flex items-center justify-center py-12">
          <Clock className="animate-spin w-8 h-8 text-blue-500 mr-3" />
          <div>
            <p className="text-lg font-medium">Loading NDC Database...</p>
            <p className="text-sm text-gray-600">Please wait while we load the medication data</p>
          </div>
        </div>
      </div>
    );
  }

  if (databaseError) {
    return (
      <div className="max-w-6xl mx-auto p-6 bg-white">
        <div className="bg-red-50 border border-red-200 rounded-lg p-4">
          <div className="flex items-center">
            <AlertCircle className="w-5 h-5 text-red-500 mr-2" />
            <h3 className="font-semibold text-red-900">Database Loading Error</h3>
          </div>
          <p className="text-red-800 mt-2">{databaseError}</p>
          <p className="text-red-700 text-sm mt-1">Please check that the NDC database file is available.</p>
        </div>
      </div>
    );
  }

  return (
    <div className="max-w-6xl mx-auto p-6 bg-white">
      <div className="mb-8">
        <h1 className="text-3xl font-bold text-gray-900 mb-2">ICD-10 to Medication Generator</h1>
        <p className="text-gray-600">Find appropriate prescription medications for ICD-10 diagnosis codes</p>
        <div className="mt-2 text-sm text-blue-600">
          Database loaded: {databaseLoading ? 'Loading...' : ndcDatabase.size.toLocaleString()} of {databaseSize.toLocaleString()} FDA-approved medications available
        </div>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6 mb-6">
        <div className="space-y-4">
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-2">
              Enter ICD-10 Codes (one per line or comma-separated)
            </label>
            <textarea
              value={inputCodes}
              onChange={(e) => setInputCodes(e.target.value)}
              placeholder="E11.9&#10;I10&#10;F32.9&#10;J45.9"
              className="w-full h-32 p-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
              rows={6}
            />
          </div>
          
          <button
            onClick={processInputCodes}
            disabled={databaseLoading}
            className="w-full bg-purple-600 text-white px-6 py-3 rounded-lg hover:bg-purple-700 disabled:opacity-50 disabled:cursor-not-allowed transition-colors font-medium"
          >
            {databaseLoading ? 'Loading...' : 'Find Medications'}
          </button>
        </div>

        <div className="bg-gray-50 p-4 rounded-lg">
          <h3 className="font-semibold text-gray-900 mb-3">Instructions</h3>
          <ul className="space-y-2 text-sm text-gray-600">
            <li>• Enter ICD-10 diagnosis codes one per line or separated by commas</li>
            <li>• Database includes {icd10ToNdcIndex.size.toLocaleString()} diagnosis codes with medication mappings</li>
            <li>• Medications are ranked by confidence scores and clinical evidence</li>
            <li>• Shows top 10 medications per diagnosis with confidence levels</li>
            <li>• Review all suggestions with healthcare providers</li>
            <li>• Export results to CSV for documentation</li>
          </ul>
          
          <div className="mt-4 p-3 bg-purple-50 rounded border border-purple-200">
            <p className="text-sm text-purple-800">
              <strong>Sample ICD-10 Codes:</strong><br/>
              E11.9 (Type 2 diabetes), I10 (Hypertension)<br/>
              F32.9 (Depression), J45.9 (Asthma)<br/>
              M25.50 (Joint pain), K21.9 (GERD)
            </p>
          </div>
        </div>
      </div>

      {results.length > 0 && (
        <div className="space-y-4">
          <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
            <h2 className="text-xl font-semibold text-gray-900">
              Medication Results ({results.length} ICD-10 codes processed)
            </h2>
            <div className="flex gap-3">
              <div className="relative">
                <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400 w-4 h-4" />
                <input
                  type="text"
                  placeholder="Search results..."
                  value={searchTerm}
                  onChange={(e) => setSearchTerm(e.target.value)}
                  className="pl-10 pr-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                />
              </div>
              <button
                onClick={exportResults}
                className="flex items-center gap-2 bg-green-600 text-white px-4 py-2 rounded-lg hover:bg-green-700 transition-colors"
              >
                <Download className="w-4 h-4" />
                Export CSV
              </button>
            </div>
          </div>

          <div className="grid gap-4">
            {filteredResults.map((result, index) => (
              <div key={index} className="border border-gray-200 rounded-lg p-4 bg-white shadow-sm">
                <div className="flex items-start justify-between mb-4">
                  <div>
                    <div className="flex items-center gap-2 mb-1">
                      <span className="font-mono text-lg font-semibold text-purple-600">
                        {result.icd10Code}
                      </span>
                      {result.status === 'found' ? (
                        <CheckCircle className="w-5 h-5 text-green-500" />
                      ) : result.status === 'suggested' ? (
                        <AlertCircle className="w-5 h-5 text-orange-500" />
                      ) : (
                        <AlertCircle className="w-5 h-5 text-red-500" />
                      )}
                    </div>
                    <h3 className="font-semibold text-gray-900 text-lg">{result.description}</h3>
                    {result.totalFound && (
                      <p className="text-sm text-gray-600 mt-1">
                        {result.totalFound} medications found (showing top 10)
                      </p>
                    )}
                  </div>
                  <span className={`px-2 py-1 rounded text-xs font-medium ${
                    result.status === 'found' 
                      ? 'bg-green-100 text-green-800' 
                      : result.status === 'suggested'
                      ? 'bg-orange-100 text-orange-800'
                      : 'bg-red-100 text-red-800'
                  }`}>
                    {result.status === 'found' ? 'Found' : result.status === 'suggested' ? 'Suggested' : 'Unknown'}
                  </span>
                </div>

                <div className="space-y-2">
                  <h4 className="font-medium text-gray-900">Recommended Medications:</h4>
                  <div className="grid gap-2">
                    {result.medications.map((med, medIndex) => (
                      <div key={medIndex} className="flex items-start justify-between p-3 bg-gray-50 rounded border-l-4 border-purple-400">
                        <div className="flex-1">
                          <div className="flex items-center gap-2 mb-1">
                            <span className="font-semibold text-gray-900">{med.name}</span>
                            <span className="font-mono text-sm text-purple-600">{med.rxCode}</span>
                            {med.confidence && (
                              <span className={`px-1 py-0.5 text-xs rounded ${
                                med.confidence >= 0.7 ? 'bg-green-100 text-green-600' :
                                med.confidence >= 0.4 ? 'bg-yellow-100 text-yellow-600' :
                                'bg-red-100 text-red-600'
                              }`}>
                                {(med.confidence * 100).toFixed(0)}%
                              </span>
                            )}
                          </div>
                          <div className="flex flex-wrap gap-2 mb-1">
                            <span className="px-2 py-1 bg-gray-100 text-gray-700 text-xs rounded">
                              {med.strength}
                            </span>
                            <span className="px-2 py-1 bg-blue-100 text-blue-700 text-xs rounded">
                              {med.class}
                            </span>
                          </div>
                          <p className="text-sm text-gray-600">{med.indication}</p>
                          {med.context && (
                            <p className="text-gray-500 text-xs italic">Context: {med.context}</p>
                          )}
                        </div>
                      </div>
                    ))}
                  </div>
                </div>
              </div>
            ))}
          </div>
        </div>
      )}

      {results.length === 0 && (
        <div className="text-center py-12 text-gray-500">
          <Upload className="w-12 h-12 mx-auto mb-4 text-gray-300" />
          <p>Enter ICD-10 codes above to find corresponding medications</p>
          <p className="text-sm mt-2">Database includes {icd10ToNdcIndex.size.toLocaleString()} diagnosis codes with medication mappings</p>
        </div>
      )}

      <div className="mt-8 bg-purple-50 border border-purple-200 rounded-lg p-4">
        <h3 className="font-semibold text-purple-900 mb-2">Database Coverage</h3>
        <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-2 text-sm text-purple-800">
          <div>• All major ICD-10 categories</div>
          <div>• Evidence-based mappings</div>
          <div>• Confidence-scored recommendations</div>
          <div>• FDA-approved medications</div>
          <div>• Multiple therapeutic options</div>
          <div>• Brand and generic drugs</div>
          <div>• Clinical context provided</div>
          <div>• Real-world usage data</div>
        </div>
        <p className="text-sm text-purple-800 mt-3">
          <strong>Total:</strong> {icd10ToNdcIndex.size.toLocaleString()} ICD-10 codes mapped to {ndcDatabase.size.toLocaleString()} medications.
        </p>
      </div>

      <div className="mt-6 bg-amber-50 border border-amber-200 rounded-lg p-4">
        <h3 className="font-semibold text-amber-900 mb-2">Clinical Documentation Notes</h3>
        <ul className="text-sm text-amber-800 space-y-1">
          <li>• Medications are ranked by confidence scores based on clinical evidence</li>
          <li>• Consider patient-specific factors before prescribing any medication</li>
          <li>• High confidence mappings (&ge;70%) are strongly evidence-based</li>
          <li>• Review contraindications, drug interactions, and patient allergies</li>
          <li>• This tool is for informational purposes and requires clinical validation</li>
        </ul>
      </div>
    </div>
  );
};

export default ICD10ToRXGenerator;