// NDC Database - Lazy loading chunks to avoid memory issues
export const databaseSize = 112092;
export const chunkCount = 12;

// Cache for loaded chunks
const chunkCache = new Map();

// Lazy load chunk function
export const loadChunk = async (chunkIndex) => {
  if (chunkCache.has(chunkIndex)) {
    return chunkCache.get(chunkIndex);
  }
  
  try {
    const chunkModule = await import(`./ndcChunk${chunkIndex}.js`);
    const chunkData = chunkModule[`ndcChunk${chunkIndex}`];
    chunkCache.set(chunkIndex, chunkData);
    return chunkData;
  } catch (error) {
    console.error(`Failed to load chunk ${chunkIndex}:`, error);
    return [];
  }
};

// Search function that searches across all chunks
export const searchNDC = async (ndcCode) => {
  const cleanNdc = ndcCode.replace(/[-\s]/g, '');
  
  // Search through chunks one by one
  for (let i = 0; i < chunkCount; i++) {
    const chunkData = await loadChunk(i);
    
    // Look for exact match first
    const exactMatch = chunkData.find(item => 
      item.NDC === ndcCode || 
      item.NDC.replace(/[-\s]/g, '') === cleanNdc
    );
    
    if (exactMatch) {
      return exactMatch;
    }
    
    // Look for partial match
    const partialMatch = chunkData.find(item => {
      const itemNdc = item.NDC.replace(/[-\s]/g, '');
      return itemNdc.includes(cleanNdc) || cleanNdc.includes(itemNdc);
    });
    
    if (partialMatch) {
      return partialMatch;
    }
  }
  
  return null;
};

// Load all data (for components that need full database - use with caution)
export const loadAllData = async () => {
  const allData = [];
  for (let i = 0; i < chunkCount; i++) {
    const chunkData = await loadChunk(i);
    allData.push(...chunkData);
  }
  return allData;
};

// Build database Map from chunks (progressive loading)
export const buildDatabase = async (onProgress) => {
  const database = new Map();
  
  for (let i = 0; i < chunkCount; i++) {
    const chunkData = await loadChunk(i);
    
    chunkData.forEach(row => {
      const ndc = row.NDC;
      if (!ndc) return;

      const proprietaryName = row.PROPRIETARYNAME || '';
      const nonproprietaryName = row.NONPROPRIETARYNAME || '';
      const primaryIcd10 = row.PRIMARY_ICD10 || '';
      const allIcd10Mappings = row.ALL_ICD10_MAPPINGS || '';
      const mappingConfidence = parseFloat(row.MAPPING_CONFIDENCE) || 0;

      // Parse the JSON ICD-10 mappings
      let icdMappings = [];
      if (allIcd10Mappings && allIcd10Mappings.includes('[')) {
        try {
          let jsonString = allIcd10Mappings;
          if (jsonString.startsWith('"') && jsonString.endsWith('"')) {
            jsonString = jsonString.slice(1, -1);
          }
          jsonString = jsonString.replace(/""/g, '"');
          icdMappings = JSON.parse(jsonString);
        } catch (e) {
          if (primaryIcd10) {
            icdMappings = [{
              icd10_code: primaryIcd10,
              description: 'Primary diagnosis code',
              confidence: mappingConfidence,
              context: 'primary indication'
            }];
          }
        }
      } else if (primaryIcd10) {
        icdMappings = [{
          icd10_code: primaryIcd10,
          description: 'Primary diagnosis code',
          confidence: mappingConfidence,
          context: 'primary indication'
        }];
      }

      database.set(ndc, {
        ndc,
        proprietaryName,
        nonproprietaryName,
        primaryIcd10,
        icdMappings,
        mappingConfidence
      });
    });
    
    // Report progress
    if (onProgress) {
      onProgress(i + 1, chunkCount, database.size);
    }
  }
  
  return database;
};

// Build database with reverse ICD-10 index (for ICD10ToRXGenerator)
export const buildDatabaseWithIndex = async (onProgress) => {
  const database = new Map();
  const icd10Index = new Map();
  
  for (let i = 0; i < chunkCount; i++) {
    const chunkData = await loadChunk(i);
    
    chunkData.forEach(row => {
      const ndc = row.NDC;
      if (!ndc) return;

      const proprietaryName = row.PROPRIETARYNAME || '';
      const nonproprietaryName = row.NONPROPRIETARYNAME || '';
      const primaryIcd10 = row.PRIMARY_ICD10 || '';
      const allIcd10Mappings = row.ALL_ICD10_MAPPINGS || '';
      const mappingConfidence = parseFloat(row.MAPPING_CONFIDENCE) || 0;

      // Parse the JSON ICD-10 mappings
      let icdMappings = [];
      if (allIcd10Mappings && allIcd10Mappings.includes('[')) {
        try {
          let jsonString = allIcd10Mappings;
          if (jsonString.startsWith('"') && jsonString.endsWith('"')) {
            jsonString = jsonString.slice(1, -1);
          }
          jsonString = jsonString.replace(/""/g, '"');
          icdMappings = JSON.parse(jsonString);
        } catch (e) {
          if (primaryIcd10) {
            icdMappings = [{
              icd10_code: primaryIcd10,
              description: 'Primary diagnosis code',
              confidence: mappingConfidence,
              context: 'primary indication'
            }];
          }
        }
      } else if (primaryIcd10) {
        icdMappings = [{
          icd10_code: primaryIcd10,
          description: 'Primary diagnosis code',
          confidence: mappingConfidence,
          context: 'primary indication'
        }];
      }

      const medicationData = {
        ndc,
        proprietaryName,
        nonproprietaryName,
        primaryIcd10,
        icdMappings,
        mappingConfidence
      };

      database.set(ndc, medicationData);

      // Build reverse index: ICD-10 codes to NDC codes
      icdMappings.forEach(mapping => {
        const icd10Code = mapping.icd10_code;
        if (!icd10Index.has(icd10Code)) {
          icd10Index.set(icd10Code, []);
        }
        icd10Index.get(icd10Code).push(medicationData);
      });
    });
    
    // Report progress
    if (onProgress) {
      onProgress(i + 1, chunkCount, database.size);
    }
  }
  
  return { database, icd10Index };
};

